<template>
  <span @click="getVerCode">
    {{ content }}
  </span>
</template>
<script>
export default {
  name: 'c-countdown',
  data() {
    return {
      content: '获取验证码',
      canClick: true,
      second: 0,
      verCodeFlag: false
    }
  },
  props: {
    phoneCode: {
      type: String,
      default: ''
    },
    totalTime: {
      type: Number,
      default: 60
    },
    getVerUrl: {
      type: String,
      default: ''
    },
    isMobileExists: {
      type: Boolean,
      default: false
    },
    isGoRegistered: {
      type: Boolean,
      default: false
    }
  },
  created() {
    this.second=this.totalTime
  },
  methods: {
    getVerUrlHTTP: function() {
      if (!this.canClick) return;
      this.canClick=false;//获取验证码是否可以点击
      this.content=`(${this.second}s)后重发`;
      let clock = window.setInterval(() => {
        this.second--;
        this.content=`(${this.second}s)后重发`;
        if (this.second < 0) {
          window.clearInterval(clock);
          this.content='获取验证码';
          this.second=60;
          this.canClick=true;
        }
      },1000);
      this.$http.post(this.getVerUrl,{'mobile': this.phoneCode}).then(response => {
        if (response.data.success){
          this.$emit('verCodeFlag',this.verCodeFlag);//判断是否可以输入验证码
        } else {
          this.$toast(response.data.errorMessage)
        }
      }, response => {
        console.log(response)
      });
    },
    getVerCode() {
      if (!this.phoneCode) {
        this.$toast('请先输入手机号');
        return
      }
      //获取验证码接口
      if (this.isMobileExists) {
        this.$http.post('/api/user/isMobileExists',{'mobile': this.phoneCode}).then(response => {
          if (this.isGoRegistered){
            if (response.data.data){
              this.getVerUrlHTTP();
            } else {
              this.$dialog.confirm({
                title: '提示',
                message: '手机号不存在，是否需要注册？',
                messageAlign: 'center'
              }).then(() => {
                this.$router.push({name: '注册'})
              }).catch(() => {})
            }
          } else {
            if (!response.data.data){
              this.getVerUrlHTTP();
            } else {
              this.$toast('您的手机号已存在')
            }
          }
        },response => {
          console.log(response)
        })
      } else {
        this.getVerUrlHTTP();
      }
    }
  }
}
</script>
<style scoped lang="scss">
span{
  color: $orange-dark;
  font-size: 15px;
}
</style>
