<template>
  <transition name="confirm-fade">
    <div class="confirm-main" v-show="showFlag" @click.stop>
      <div class="confirm-wrapper">
        <div class="confirm-content">
          <div class="title-text">
            <p class="title">
              {{ title }}
            </p>
            <p class="text">
              {{ text }}
            </p>
          </div>
          <slot></slot>
          <div class="operate">
            <div @click="cancel" class="operate-btn left">
              {{ cancelBtnText }}
            </div>
            <div @click="confirm" class="operate-btn">
              {{ confirmBtnText }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'cConfirm',
  props: {
    title: {
      type: String,
      default: ''
    },
    text: {
      type: String,
      default: ''
    },
    confirmBtnText: {
      type: String,
      default: '确定'
    },
    cancelBtnText: {
      type: String,
      default: '取消'
    }
  },
  data() {
    return {
      showFlag: false
    }
  },
  methods: {
    show() {
      this.showFlag = true
    },
    hide() {
      this.showFlag = false
    },
    cancel() {
      this.hide()
      this.$emit('cancel')
    },
    confirm() {
      this.$emit('confirm')
    }
  }
}
</script>
<style lang="scss" scoped>
.confirm-main{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(0,0,0,.3);
  &.confirm-fade-enter-active {
    animation: confirm-fadein 0.3s;
    .confirm-content {
      animation: confirm-zoom 0.3s
    }
  }
  .confirm-wrapper{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 1001;
    border-radius: 8px;
    .confirm-content {
      background: #fff;
      width: 270px;
      .title-text {
        padding-bottom: 10px;
      }
      .title{
        padding: 19px 15px 2px ;
        line-height: 22px;
        text-align: center;
        font-size: 18px;
        color: #000000;
      }
      .text {
        text-align: center;
        font-size: 14px;
        color: #666666;
      }
      .operate{
        display: flex;
        align-items: center;
        text-align: center;
        font-size: 16px;
        .operate-btn{
          flex: 1;
          line-height: 22px;
          padding: 10px 0;
          border-top: 1px solid #ddd;
          color: #f37836;
          &.left{
            border-right: 1px solid #ddd;
          }
        }
      }
    }
  }
}
@keyframes confirm-fadein{
  0%{
    opacity: 0
  }
  100% {
    opacity: 1
  }
}
@keyframes confirm-zoom {
  0% {
    transform: scale(0)
  }
  50% {
    transform: scale(1.1)
  }
  100% {
    transform: scale(1)
  }
}
</style>

