export default {
  set_userInfo(state,value,key){
    if (!key) {
      state.USERINFO = value
    } else {
      state.USERINFO[key] = value
    }
  },
  set_Province(state,value) {
    state.Province = value
  },
  set_City(state,value) {
    state.City = value
  },
  set_CountyCode(state,value) {
    state.CountyCode = value
  },
  set_receiptAreaList(state,value) {
    state.receiptAreaList = value
  },
  set_hospitalList(state,value) {
    state.hospitalList = value
  },
  set_hospital(state,value) {
    state.hospital = value
  },
  set_investigativeInformationDto(state,value) {
    state.investigativeInformationDto=value
  }
}
