<template>
  <div class="cell-main">
    <div class="cell-left" :style="{ fontWeight: fontBold }">
      {{ title }}<i v-if="mark">
        *
      </i>
    </div>
    <div class="cell-right" v-if="rightType === 'select'" @click="clickSelect">
      <div>{{ value || "请选择" }}</div><i class="iconfont icon-tiaozhuan"></i>
    </div>
    <div class="cell-right" v-if="rightType === 'text'">
      <span>{{ value || "-" }}</span>
    </div>
    <div class="cell-right" v-if="rightType === 'input'">
      <input type="text" v-model="inputData" :placeholder="placeholderText">
    </div>
    <div class="cell-right" v-if="rightType === 'switch'">
      <van-switch style="margin-top: 10px" v-model="inputData" activeColor="rgb(243,120,54)" inactiveColor="#c9c9c9" />
    </div>
  </div>
</template>
<script>
export default {
  name: 'cCell',
  props: {
    title: {
      type: String,
      default: ''
    },
    mark: {
      type: Boolean,
      default: false
    },
    rightType: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    fontBold: {
      type: Number,
      default: 400
    },
    placeholder: {
      type: String,
      default: ''
    }
  },
  data() {
    return {}
  },
  computed: {
    placeholderText() {
      if(this.placeholder) {
        return this.placeholder
      } else {
        return '请输入' + this.title
      }
    },
    inputData: {
      get() {
        if (this.rightType === 'switch') {
          if (this.value === '1') {
            return true
          } else {
            return false
          }
        }
        return this.value
      },
      set(val) {
        if (this.rightType === 'switch') {
          if (val) {
            val='1'
          } else {
            val='0'
          }
        }
        this.$emit('input', val)
      }
    }
  },
  methods: {
    clickSelect() {
      this.$emit('clickSelect')
    }
  }
}
</script>
<style lang="scss" scoped>
.cell-main{
  position: relative;
  padding: 0 20px;
  height: 56px;
  display: flex;
  .cell-left {
    width: 35%;
    height: 56px;
    line-height: 56px;
    color: rgb(26,26,26);
    font-family: 'PingFang-SC-Medium';
    i{
      font-size: 16px;
      margin-left: 0px;
      color: rgb(241,100,28);
    }
  }
  .cell-right{
    height: 56px;
    line-height: 56px;
    flex: 1;
    width:65%;
    text-align: right;
    color: rgb(77,77,77);
    font-size: 14px;
    position: relative;
    padding-right: 14px;
    >div{
      text-overflow:ellipsis;
      white-space: nowrap;
      overflow: hidden;
      text-align: 14px;
    }
    i{
      margin-left: 4px;
      font-size: 14px;
      position: absolute;
      top: 50%;
      transform: translate(0,-50%)
    }
    input{
      text-align: right;
      padding-right:2px;
      font-size:14px;
      line-height: 14px;
      padding: 2px 0;
      outline: 0;
      line-height: 18px;
      width: 100%;
      // min-width: 200px;
    }
    span{
      text-align: right;
      padding-right:2px;
    }
  }
  &:before {
    position: absolute;
    content: ' ';
    left: 15px;
    right: 15px;
    top: 0px;
    height: 1px;
    border: none;
    background:rgb(204,204,204);
    @media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
      transform: scaleY(0.333)
    }
    @media (-webkit-min-device-pixel-ratio: 2),(min-device-pixel-ratio: 2) {
      transform: scaleY(.5)
    }
  }
  // &:last-child::after{
  //   display:none
  // }
}
</style>

