const seach = () => import('@/components/qualification/child/seach.vue')
const address = () => import('@/components/qualification/child/address.vue')
const receiptArea = () => import('@/components/qualification/child/receiptArea.vue')
const hospital = () => import('@/components/qualification/child/hospital.vue')
export default {
  path: '/qualification',
  name: '资质申请',
  component: () => import('@/components/qualification/qualification.vue'),
  meta: {title: '资质申请'},
  children: [{
    path: '/qualification/seach/:id',
    name: '资源医院搜索',
    meta: {title: '资源医院搜索'},
    component: seach
  },{
    path: '/qualification/receiptArea',
    name: '接单地区',
    meta: {title: '接单地区'},
    component: receiptArea,
    children: [{
      path: '/qualification/receiptArea/address/:id',
      name: '地区选择',
      meta: {title: '地区选择'},
      component: address
    }]
  },{
    path: '/qualification/hospital',
    name: '资源医院',
    meta: {title: '资源医院'},
    component: hospital,
    children: [{
      path: '/qualification/hospital/seach/:id',
      name: '资源医院搜索',
      meta: {title: '资源医院搜索'},
      component: seach
    }]
  }]
}