export default {
  chang_userInfo({commit}, userInfo, key) {
    commit('set_userInfo', userInfo, key)
  },
  change_Province({commit}, Province) {
    commit('set_Province', Province)
  },
  change_City({commit}, City) {
    commit('set_City', City)
  },
  change_CountyCode({commit}, CountyCode) {
    commit('set_CountyCode', CountyCode)
  },
  change_receiptAreaList_delOne({commit, state}, _cityCode) {
    if(!_cityCode) return
    let _receiptAreaList = state.hospitalList.filter((item) => {
      return item.hospitalCode != _cityCode
    })
    commit('set_hospitalList', _receiptAreaList)
  },
  change_receiptAreaList_addOne({commit, state}, _cityObj) {
    if (!_cityObj) {
      return;
    }
    let isHaveOneData = state.receiptAreaList.some((item) => {
      return item.cityCode === _cityObj.cityCode
    })
    if (isHaveOneData) {
      return
    } else {
      commit('set_receiptAreaList', [...state.receiptAreaList,_cityObj])
    }
  },
  change_hospitalList_delOne({commit, state}, _code) {
    console.log(_code)
    console.log(state.hospitalList)
    let hospitalList = state.hospitalList.filter((item) => {
      return item.hospitalCode !== _code
    })
    commit('set_hospitalList', hospitalList)
  },
  change_hospitalList_addOne({commit, state}, _hospitalObj) {
    if (!_hospitalObj) {
      return;
    }
    let isHaveOneData = state.hospitalList.some((item) => {
      return item.hospitalCode === _hospitalObj.hospitalCode
    })
    console.log(isHaveOneData)
    if (isHaveOneData) {
      return
    } else {
      commit('set_hospitalList', [...state.hospitalList,_hospitalObj])
    }
  },
  addHospital({commit}, hospital) {
    if (hospital) {
      commit('set_hospital', hospital)
    }
  },
  change_investigativeInformationDto({state,commit},valueObj) {
    if (valueObj.key) {
      state.investigativeInformationDto[valueObj.key].value = valueObj.value
    } else {
      state.investigativeInformationDto = valueObj
    }
    let investigativeInformationDto=state.investigativeInformationDto
    commit('set_investigativeInformationDto',investigativeInformationDto)

  }
}
