<template>
  <div class="scan-info-main" :style="style">
    <div class="input-content">
      <input type="file" accept="image/*" @change="onloadImg">
    </div>
    <div class="loading" v-show="isShowLoading">
      <van-loading class="loading-icon" type="spinner" />
    </div>
  </div>
</template>

<script>
const back = require('@/assets/imgs/backId@2x.png')
const positive = require('@/assets/imgs/positive@2x.png')
export default {
  name: 'cScanInfo',
  data() {
    return {
      back,
      positive,
      background: '',
      isShowLoading: false
    }
  },
  props: {
    type: {
      type: String,
      default: 'positive'
    },
    imgId: {
      type: String,
      default: ''
    }
  },
  computed: {
    style() {
      return {backgroundImage: 'url('+ this.background +')'}
    }
  },
  created() {
    this.background = this[this.type]
  },
  methods: {
    upload(formData) {
      this.isShowLoading = true
      this.$http.post(
        '/api/file/upload',
        formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
      ).then(response => {
        this.isShowLoading = false
        if(response.data.success) {
          this.$emit('addImg',{fileId: response.data.data.fileId})
        } else {
          this.$toast(response.data.errorMessage)
        }
      },err => {
        this.isShowLoading = false
        console.log(err)
      })
    },
    onloadImg(e) {
      let file = (e.target.files[0])
      e.target.value = ''
      if (file.size/1024>1024*10) {
        this.$toast('上传图片不应超过10M')
        return
      } else if(file.size/1024<1024) {
        let formData = new FormData()
        formData.append('file', file)
        this.upload(formData)
      } else {
        this.compress(file,this.upload)
      }
    },
    compress(file,callback) {
      let _self = this
      let ready = new FileReader()
      ready.readAsDataURL(file);
      ready.onload=function(){
        var img = new Image()
        img.src = this.result
        img.onload = function() {
          var that = this
          var w = that.width
          var h = that.height
          var scale = w/h
          var canvas = document.createElement('canvas')
          var ctx = canvas.getContext('2d')
          let canvasWidth = Math.min(w,600)
          canvas.width = canvasWidth
          canvas.height = parseInt(canvasWidth/scale)
          ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
          var base64 = canvas.toDataURL('image/jpeg',1.0);
          var blobDate = _self.uriToBlob(base64)
          let formData = new FormData()
          formData.append('file', blobDate,'file_' + Date.parse(new Date()) + '.png')
          callback(formData)
        }
      }
    },
    uriToBlob: function(urlData) {
      var arr = urlData.split(','), mime = arr[0].match(/:(.*?);/)[1],bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    }
  },
}
</script>
<style lang="scss" scoped>
.scan-info-main {
  position: relative;
  background-size: 100% 100%;
  .input-content {
    position: relative;
    width: 100%;
    height: 100%;
    input {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
    }
  }
  .loading {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    .loading-icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
</style>
