<template>
  <van-popup class="address-main" v-model="addressShow" :closeOnClickOverlay="false" position="bottom">
    <div class="address-content" ref="address">
      <h2>
        选择地址<van-icon class="close" @click="clickModel" name="close" />
      </h2>
      <div class="choose-title">
        <div @click="tabClick(index)" v-show="!!val" :class="{active: titleList.length == number && tabActive === index}" v-for="(val , index) in titleList" :key="index">
          {{ val.codeName }}
        </div>
        <div v-show="titleList.length != number" class="text-color-theme border-1px active">
          请选择
        </div>
      </div>
      <div class="city-picker" @touchstart="_startDrag" @touchmove="_onDrag" @touchend="_endDrag" @touchcancel="_endDrag">
        <div class="city-picker-content" :style="swiperStyle">
          <!-- 省 -->
          <div class="city-picker-item">
            <ul>
              <li class="item" v-for="(val,index) in get_Province" :key="index" @click="_clickPrevice(val,index, 0)" :class="{ 'text-color-theme':!!titleList[0] && val.codeCode === titleList[0].codeCode}">
                {{ val.codeName }}
              </li>
            </ul>
          </div>
          <!-- 市 -->
          <div class="city-picker-item">
            <ul>
              <li class="item" v-for="(val,index) in citys" :key="index" @click="_clickPrevice(val,index, 1)" :class="{ 'text-color-theme':!!titleList[1] && val.codeCode === titleList[1].codeCode}">
                {{ val.codeName }}
              </li>
            </ul>
          </div>
          <!-- 区 -->
          <div class="city-picker-item">
            <ul>
              <li class="item" v-for="(val,index) in countys" :key="index" @click="_clickPrevice(val,index, 2)" :class="{ 'text-color-theme':!!titleList[2] && val.codeCode === titleList[2].codeCode}">
                {{ val.codeName }}
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </van-popup>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'cAddress',
  props: {
    number: {
      type: Number,
      default: () => 2
    }
  },
  data() {
    return {
      addressShow: false,
      titleList: [],
      offset: 0,
      touch: {
        offset: 0,
        move: false
      },
      citys: [],
      countys: [],
      tabActive: 0
    }
  },
  computed: {
    ...mapGetters(['get_Province','get_City','get_CountyCode']),
    swiperStyle() {
      return{
        transform: `translate3d(${this.offset}px, 0, 0)`,
        transition: this.touch.moved ? 'none' : '.6s cubic-bezier(0.18, 0.89, 0.32, 1)'
      }
    }
  },
  watch: {
    tabActive(val,old) {
      this.offset = this.touch.offset + (old-val) * this.addressWidth
      this.touch.offset = this.offset
    }
  },
  created() {
    console.log(this.get_Province)
  },
  mounted() {
    this.addressWidth = window.innerWidth
  },
  methods: {
    _clickPrevice(val, index, i) {
      console.log(val, index, i)
      this.titleList[i] = val
      this.tabActive = Math.min(i + 1, 2)
      if(i >= this.number-1){
        this.$emit('close',this.titleList)
      }else if( i === 0){
        this.titleList = this.titleList.slice(0,1)
        this.citys = this.get_City.filter((item) => {
          return (item.parentCode === val.codeCode)
        })
      }else if( i === 1){
        this.titleList = this.titleList.slice(0,2)
        this.countys = this.get_CountyCode.filter((item) => {
          return (item.parentCode === val.codeCode)
        })
        console.log(this.countys)
      }
    },
    _startDrag(e) {
      this.touch.initiated = true
      this.touch.moved = false
      const touch = e.touches[0]
      this.touch.startX = touch.pageX
      this.touch.startY = touch.pageY
      this.touch.deractionX = 0
      this.touch.startTimes = new Date().getTime()
    },
    _onDrag(e) {
      if(!this.touch.initiated) return

      const touch = e.touches[0]
      const deltaX = touch.pageX - this.touch.startX
      const deltaY = touch.pageY -this.touch.startY

      if (Math.abs(deltaY) > Math.abs(deltaX)) return;
      this.touch.x = deltaX
      this.touch.moved = true

      this.touch.deractionX = deltaX > 0 ? 1 : deltaX < 0 ? -1 : 0

      if(this.titleList.length < this.tabActive + 1 && this.touch.deractionX < 0) return
      e.preventDefault()
      this.offset = (deltaX + this.touch.offset) > 0 ? 0 : (deltaX + this.touch.offset) < -(this.number - 1) * this.addressWidth ? - (this.number - 1) * this.addressWidth : deltaX + this.touch.offset
    },
    _endDrag() {
      if(!this.touch.initiated && this.touch.deractionX === 0 || (this.titleList.length < this.tabActive + 1 && this.touch.deractionX < 0)) return
      if((this.tabActive - this.touch.deractionX) < 0 || (this.tabActive - this.touch.deractionX > this.number-1)) return;
      this.touch.moved = false
      this.touch.initiated = false
      const times = new Date().getTime() - this.touch.startTimes
      if(times < 300) {
        if( Math.abs(this.touch.x) > 30){
          this.tabActive -= this.touch.deractionX
        }else{
          this.offset = this.touch.offset
        }
      } else {
        if(Math.abs(this.touch.x) > .5*this.addressWidth) {
          this.tabActive -= this.touch.deractionX
        }else{
          this.offset = this.touch.offset
        }
      }
    },
    tabClick(index) {
      this.tabActive = index
      this.touch.moved = false
    },
    clickModel() {
      this.$emit('close')
      this.titleList = []
      this.hide()
    },
    show() {
      this.addressShow = true
    },
    hide() {
      this.addressShow = false
    }
  }
}
</script>
<style lang="scss" scoped>
.address-main {
  .address-content {
    position:relative;
    background:#ffffff;
    z-index:2002;
    h2{
      text-align:center;
      font-size:16px;
      padding:10px 0;
      color:#afa8a8;
      position:relative;
      .close{
        position:absolute;
        right:20px;
        top:50%;
        font-size: 16px;
        transform: translate(0,-50%);
      }
    }
    .choose-title{
      width:100%;
      border-bottom:.5px solid #dfdfdf;
      border-top:.5px solid #dfdfdf;
      display:flex;
      font-size:14px;
      align-items:stretch;
      >div{
        padding:15px 0px;
        margin:0 15px;
        &.active{
          border-width: 0 0 .5px 0;
          border-bottom: 0.5px solid $color-theme;
          color: $color-theme;
        }
      }
    }
    .city-picker{
        height:400px;
        overflow:hidden;
        width:100%;
        .city-picker-content{
          width:300%;
          height:100%;
          display:flex;
        }
        .city-picker-item{
            overflow:scroll;
            height:100%;
            flex:1;
            -webkit-overflow-scrolling:touch;
            .item{
              padding:10px 15px;
              font-size:14px;
              display:flex;
              cursor: pointer;
              &.text-color-theme{
                color:$color-theme;
                &:after{
                  display:block;
                  content:'\2713';
                  margin-left:4px;
                }
              }
            }
        }
    }
  }
}
</style>
