<template>
  <div class="cNavBar-main">
    <div class="nav-left" @click="clickLeft">
      <slot name="left"></slot>
    </div>
    <div class="nav-center">
      <slot name="center"></slot>
    </div>
    <div class="nav-right" @click="clickRight">
      <slot name="right"></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: 'cNavBar',
  props: {
    path: {
      type: String,
      default: ''
    }
  },
  methods: {
    clickRight: function(){
      this.$emit('clickRight')
    },
    clickLeft: function() {
      if(this.path) {
        // this.$emit('clickLeft')
        this.$router.push({
          path: this.path
        })
      } else {
        this.$router.back()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.cNavBar-main{
  height: 44px;
  line-height: 44px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 200;
  background: #fff;
  .nav-right{
    position: absolute;
    right:15px;
    top:0;
    height: 44px;
    font-size: 16px;
    color: rgb(26,26,26)
  }
  .nav-left{
    position: absolute;
    left:15px;
    top:0;
    height: 44px;
  }
  .nav-center{
    width:100%;
    height: 44px;
    text-align: center;
    font-size: 18px;
    color: rgb (26,26,26)
  }
}
</style>

