<template>
  <div class="verticalScroll">
    <div class="title">
      <img class="img_name" :src="verticalScrollObj.title" alt="">
    </div>
    <div class="content">
      <div class="slide" ref="slider">
        <div class="slide-group" ref="slideGroup">
          <div class="slider-item" v-for="(name,index) in verticalScrollObj.message" :key="index">
            <span>
              {{ name }}
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  name: 'bulletin-board',
  props: {
    verticalScrollObj: {
      type: Object,
      default: () => {}
    },
    loop: {
      type: Boolean,
      default: true,
    },
    autoPlay: {
      type: Boolean,
      default: true,
    },
    interval: {
      type: Number,
      default: 1000
    }
  },
  data() {
    return {
      currentPageIndeY: 0
    }
  },
  mounted() {
    this.setSliderHeight(); //设置轮播图的宽度
    this.init();
    this.play();
  },
  methods: {
    setSliderHeight() {
      this.children = this.$refs.slideGroup.children;
      let height =0;
      let sliderHeight = this.$refs.slider.clientHeight;
      for (let i = 0; i < this.children.length; i ++) {
        height += sliderHeight;
      }
      this.$refs.slideGroup.style.height = height + 'px';
    },
    init() {
      var vm = this;
      // 实例化scroll
      this.scroll = new BScroll(this.$refs.slider, {
        scrollX: false,
        scrollY: true,
        momentum: false,//关闭或者打开惯性运动的执行
        snap: true,//隔断（翻页）
        snapLoop: this.loop,//无限滚动
        snapThreshold: 0,
        snapSpeed: 400,//滑动的时间
      })
      this.scroll.on('scrollEnd', () => {
        let pageIndeY = this.scroll.getCurrentPage().pageY
        this.currentPageIndeY = pageIndeY;
        if (vm.autoPlay) {
          vm.play();
        }
      })
      this.scroll.on('beforeScrollStart', () => {
        if (vm.autoPlay) {
          clearTimeout(vm.timer);
        }
      })
    },
    play() {
      this.children=this.$refs.slideGroup.children
      let pageIndeY = this.currentPageIndeY
      if (this.loop) {
        pageIndeY += 1
      }
      if (pageIndeY > this.children.length-1){
        pageIndeY = 0
      }
      var vm = this;
      this.timer = setTimeout(() => {
        vm.scroll.goToPage(0, pageIndeY , 400);//跳转到的页数 初始化页数 滑动总时间
      },vm.interval)
    }
  }
}
</script>
<style scoped lang="scss">
.slide{
  height: 20px;
}
.slide-group{
  width: 100%;
  .slider-item{
    width: 100%;
    height: 20px;
  }
}
.verticalScroll{
  width: 100%;
  height: 50px;
  padding: 15px 0;
  display: flex;
  .title{
    width:60px;
    height:20px;
    margin-right: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    .img_name{
      width:29.5px;
      height:14.5px;
    }
    border:{
      right: 1px solid rgb(224,224,224);
    };
  }
  .content{
    flex: 1;
    line-height: 20px;
    color: rgb(26,26,26);
    overflow: hidden;
  }
  .van-notice-bar__wrap{
    height: 20px;
  }
  .van-notice-bar{
    height: 20px;
  }
}
</style>
