import Vue from 'vue'
import VueResource from 'vue-resource'
import VeeValidate from 'vee-validate'
import App from './App.vue'
import router from './router/router.js'
import store from './store'
import ComponentsMobile from '@hhm1999/vue-components-mobile'
import 'vant/lib/index.css';
import '@/assets/iconfont/iconfont.css'
import '@/css/reset.scss'
import './components/common/index.js'
import { Lazyload,Button,Icon,Checkbox,Popup,NoticeBar,Field,Tag,Cell,List,Tab,Tabs,
  Picker,Dialog,Loading,Panel,Switch } from 'vant';
Vue.use(Lazyload).use(Button).use(Icon).use(Checkbox).use(Popup).use(NoticeBar)
  .use(Field).use(Tag).use(Cell).use(List).use(Tab).use(Tabs).use(Picker).use(Dialog).use(Panel)
  .use(Loading).use(Switch)
Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VeeValidate)
Vue.use(ComponentsMobile)
//拦截器
Vue.http.options.crossOrigin = true;
Vue.http.options.emulateHTTP = true;
Vue.http.interceptors.push((request, next) => {
  next((response) => {
    if (response.data.errorCode === 'unauthorized') {
      Vue.$router.push({name: '登录'});
      return;
    }
    return response;
  });
});
const _options = {
  enableHighAccuracy: true,
  maximumAge: 1000
}
new Vue({
  router,
  store,
  render: h => h(App),
  created () {
    (process.env.NODE_ENV === 'development') && require('../config/mock/index.js').default.init()
    this.httpInterceptors()
    this.getPositionByGeolocation()
  },
  methods: {
    httpInterceptors () {
      Vue.http.interceptors.push(function(request) {
        if (request.body && request.body.interceptorsUrlIgnore) {
          delete request.body.interceptorsUrlIgnore
        } else if (request.params && request.params.interceptorsUrlIgnore) {
          delete request.params.interceptorsUrlIgnore
        } else {
          request.url = '/api' + request.url
        }
      })
    },
    getPositionByGeolocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
          alert(position)
        },(error) => {
          console.log(error)
          switch(error.code){
          case 1:
            alert('位置服务被拒绝');
            break;
          case 2:
            alert('暂时获取不到位置信息');
            break;
          case 3:
            alert('获取信息超时');
            break;
          case 4:
            alert('未知错误');
            break;
          }
        }, _options)
      } else {
        alert('不支持定位')
      }
    }
  }
}).$mount('#app')
